    text-align: left;
    font-family: "Amatic SC";
    font-size: 2em;
    font-weight: bold;
}

.lobby input {
    font-family: "Amatic SC";
    font-size: 2em;
    line-height: 55px;
    border: none;
    background: none;
}

.lobby #joinCode, .lobby #nickname{
    background: white;
}


.lobby .textBox {
    padding-left: 20px;
    padding-right: 20px;
    border: 2px solid black;
    width: 100%;
    display: block;
    margin: auto;
}

.lobby #container {
    margin: auto;
    width: 50%;
    min-height: 43vh;
}

.lobby #split-container{
    padding: 10px;
    display: table;
    width: 75%;
    margin: auto;
    min-height: 47vh;
}

.lobby #split-container #left {
    display: table-cell;
    min-width: 50%;

}

.lobby #split-container #right {
    display: table-cell;
    min-width: 50%;
}

.lobby .slider {
    -webkit-appearance: none;
    width: 90%;
